<template>
    <div class="w-full md:max-w-4xl p-2 mx-auto md:flex justify-around flex-wrap items-center bg-white rounded-lg shadow-md overflow-hidden">
        <div class="w-full md:w-1/2 md:p-4 md:-mt-24">
            <h2 class="text-3xl md:text-3xl text-brand font-bold">Welcome to GCPSC</h2>
            <span class="hidden md:block text-brand sm:text-center font-bold text-md">(Genesis Career & Psychological Support Centre)</span>
            <div class="hidden md:block">
              <img src="../../../assets/image/wall.png" alt="picture" class="sx-invisible">    
            </div>
        </div>

        <div class="pt-1 w-full md:w-1/2 md:p-4">
            <span class="text-xl md:text-2xl font-bold text-brand mb-2 md:mb-4"> Appointment Schedule </span>
            <div class="md:bg-gray-200 p-2  flex justify-between md:justify-around items-center md:mx-auto rounded mt-2"> 
                <label v-for="type in suportType" :key="type.id">
                    <div :class="{'bg-white text-brand' : mentorType == type.id }"
                    class="cursor-pointer bg-white overflow-hidden relative text-left md:p-2 shadow rounded hover:shadow-lg">
                        <input type="radio" v-model="mentorType" :value="type.id" class="absolute -left-full"  />
                        <span class="p-2 hover:text-brand text-sm md:tex-4xl">{{ type.name }}</span>
                    </div>
               </label>
            </div>

            <div v-if="mentorType" class="flex bg-white items-center rounded mt-4">
                <!-- <input type="checkbox"  id="option-3" name="mentor"> -->
                <label for="option-3" class="bg-white flex justify-evenly ml-3 mr-3 rounded cursor-pointer shadow hover:shadow-lg p-2 transition-all option-3">
                        <div class="bg-gray-200 rounded absolute dot -left-full"></div>
                    <span class="text-gray-600" @click="historyModel =! historyModel"> Previous History </span>
                </label>
            </div>

             <div class="">
                <div class="mt-2">
                    <DatePicker :available-dates='availableDates' v-model='date' />
                </div>
                <div class="grid grid-cols-3 gap-2 mt-2">
                    <label v-for="chamber in availableChamber" :key="chamber.id">
                        <div :class="{'bg-green-500 text-white' : chamberId == chamber.id , 'bg-gray-200 cursor-context-menu': !chamber.status }"
                        class="cursor-pointer overflow-hidden relative text-center border py-1.5 text-lg rounded shadow">
                            <input type="radio" v-model="chamberId" :value="chamber.id" class="absolute -left-full" />
                            <span class="p-2 text-sm md:text-md">{{ chamber.name }}</span>
                        </div>
                    </label>
                </div>
                <div class="grid grid-cols-2 gap-2 mt-2">
                    <label v-for="time in availableTime" :key="time.id">
                        <div :class="{'bg-green-500 text-white' : scheduleId == time.id }"
                            class="cursor-pointer overflow-hidden relative text-center border my-1.5 text-lg rounded shadow">
                            <input type="radio" v-model="scheduleId" :value="time.id" class="absolute -left-full" />
                            <span class="p-2 text-xs md:text-sm">{{ time.time_schedule.start_time }} - {{ time.time_schedule.end_time }}</span>
                            </div>
                    </label>
                </div>
            </div>
            <div class="p-4" >
                <btn variant="bg-brand" type="submit"  @click="toggleModel =! toggleModel" class="bg-brand text-white text-base md:text-lg">Submit Request</btn>
            </div>
        </div>
    </div>


<!-- Previous History Modal -->

    <div>
        <div class="fixed overflow-x-hidden overflow-y-auto inset-0 flex justify-center items-center z-50" v-if="historyModel ">
            <div class="relative mx-auto w-auto max-w-xl">
                <div class="bg-white w-full rounded shadow-2xl flex flex-col">
                    <div class="modal-body relative p-4 text-lg">
                        <div class="text-left p-4" @click="mentorSelect(mentor.last_appointment.requested_mentor_id)">
                            <p>Appointment Type : {{ suportType[0].name }} </p> 
                            <p>Questions : {{ lastAppointments.last_appointment.questions.length ? lastAppointments.last_appointment.questions : '' }}</p> 
                            <p>Payable : {{ lastAppointments.last_appointment.payable ? lastAppointments.last_appointment.payable:"Free" }}</p> 
                            <p>Date: {{ lastAppointments.last_appointment.schedule.date }}</p> 
                            <p>Time Slot: {{ lastAppointments.last_appointment.schedule.time_schedule.start_time }} - {{ lastAppointments.last_appointment.schedule.time_schedule.end_time }}</p> 
                            <p>Chamber Place: {{ lastAppointments.last_appointment.schedule.chamber.name }}</p>
                        </div>
                    </div>
                    <div class="modal-footer flex justify-between items-center p-2 border-t border-gray-200 rounded-b-md">
                        <button type="button"
                            class="inline-block px-6 py-2 bg-brand text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-purple-700 hover:shadow-lg focus:bg-purple-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-purple-800 active:shadow-lg transition duration-150 ease-in-out"
                            @click="historyModel=false">
                            Close
                        </button>
                        
                        <div
                        class="inline-block px-6 py-2 bg-brand text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-purple-700 hover:shadow-lg focus:bg-purple-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-purple-800 active:shadow-lg transition duration-150 ease-in-out">
                            <input type="radio" class="absolute -left-full" />
                            <span class="p-2" @submit.prevent="postData" >Confirm</span>
                        </div>

                    </div>
                </div>
            </div>
        </div>
        <div class="absolute inset-0 z-40 opacity-25 bg-gray-400" v-if="historyModel"></div>
    </div>

<!-- preview model -->

    <div>
        <div class="fixed overflow-x-hidden overflow-y-auto inset-0 flex justify-center items-center z-50" v-if="toggleModel">
            <div class="relative mx-auto w-auto max-w-xl">
                <div class="bg-white w-full rounded shadow-2xl flex flex-col">
                    <div class="modal-body relative p-4">
                        <div class="grid grid-cols-4 gap-4 mb-2">
                                <label for="" class="col-span-1">Chamber</label>
                                <input type="text" v-model="chamber" class="col-span-3 cursor-not-allowed" readonly />
                            </div>
                            <div class="grid grid-cols-4 gap-4 mb-2">
                                <label for="" class="col-span-1">Date</label>
                                <input type="date" class="col-span-3" v-model="dates" readonly>
                            </div>
                            <div class="grid grid-cols-4 gap-4 mb-2">
                                <label for="" class="col-span-1">Time</label>
                                <input type="text" class="col-span-3" v-model="stimeEtime" readonly>
                            </div>
                            <div class="grid grid-cols-4 gap-4 mb-2">
                                <label for="" class="col-span-1">Question</label>
                                 <div class="col-span-3 grid grid-cols-5 gap-2">
                                    <div class="col-span-full">
                                        <div class="grid grid-cols-5 mb-2"  v-for="(question, index) in questions" :key="index">
                                            <InputTextArea
                                                class="col-span-4 py-2 p-2 border"
                                                v-model="questions[index].question"
                                            />
                                            <button class="col-span-1 text-red-500" @click="remove(index)">
                                                &#x274C;
                                            </button>
                                        </div>
                                    </div>
                                    <button type="button" @click="addQuestions()" class="bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white p-2 border border-blue-500 hover:border-transparent rounded"> + Add</button>
                                </div>
                            </div>
                    </div>
                    <div class="modal-footer flex flex-shrink-0 flex-wrap items-center justify-between p-2 border-t border-gray-200 rounded-b-md">
                        <button type="button"
                            class="inline-block px-6 py-2 bg-purple-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-purple-700 hover:shadow-lg focus:bg-purple-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-purple-800 active:shadow-lg transition duration-150 ease-in-out"
                            @click="toggleModel=false">
                            Close
                        </button>
                        <button type="submit" @click="postData()"
                            class="inline-block px-6 py-2 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out ml-1">
                            Confirm
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <div class="absolute inset-0 z-40 opacity-25 bg-gray-400" v-if="toggleModel"></div>
    </div>

<!-- Center model -->

    <div>
        <div class="fixed overflow-x-hidden overflow-y-auto inset-0 flex justify-center items-center z-50" v-if="centertoggleModel">
            <div class="relative mx-auto w-auto max-w-xl">
                <div class="bg-white w-full rounded shadow-2xl flex flex-col">
                    <div class="modal-body relative p-4">
                        <p class="text-center bg-green-200 p-2 rounded text-lg">Dear Doctor, this is from GCPS to remind you, your appointment on <span v-html="dates" readonly/> from 
                        <span v-html="stimeEtime"></span> in <span v-html="chamber"></span>.</p>
                        <p class="text-xl bg-green-500 rounded">Place confirm your appoitment</p>
                    </div>
                    <div class="modal-footer flex flex-shrink-0 flex-wrap items-center justify-end p-2 border-t border-gray-200 rounded-b-md">
                        <button type="button"
                            class="inline-block px-6 py-2 bg-purple-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-purple-700 hover:shadow-lg focus:bg-purple-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-purple-800 active:shadow-lg transition duration-150 ease-in-out"
                            @click="centertoggleModel=false">
                            Close
                        </button>
                        <button type="button"
                            class="inline-block px-6 py-2 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out ml-1">
                            Confirm
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <div class="absolute inset-0 z-40 opacity-25 bg-gray-400" v-if="centertoggleModel"></div>
    </div>

















</template>

<script>
import Btn from '../../../components/btn.vue';
import Select from '../../../components/select.vue';
import InputText from '../../../components/input-text.vue';
import InputTextArea from '../../../components/input-text-area.vue';
import Spinner from '../../../components/spinner.vue';
import { Calendar, DatePicker } from 'v-calendar';
import 'v-calendar/dist/style.css';
import { LocationMarkerIcon } from "@heroicons/vue/solid";
import { CalendarIcon } from "@heroicons/vue/solid";
import axios from "../../../store/axios";

import { computed } from '@vue/runtime-core';

export default {
    components: { 
        Btn,
        InputText, 
        Spinner, 
        Btn,
        InputTextArea,
        DatePicker,
        Calendar,
        LocationMarkerIcon,
        CalendarIcon,
        Select

    },
    data() {
        return {
            toggleModel:false,
            centertoggleModel:false,
            historyModel:false,
            chamberId : '',
            date: '',
            scheduleId: '',
            txt:'',
            user_id:16,
            questions: [{
                question:''
            }],
            type: '',
            requested_mentor_id: '',
            toggleModel:false,
            mentorType:'',
            preHistory:'',
            mentorId:'',
            dateTime:''
        }
    },
     created() {
        this.$store.dispatch('schedule/getSchedules');
        this.$store.dispatch('schedule/getPreviousHistory');
    },

    watch: {
        date(newDate, oldDate) {
            this.searchSchedule();
        },
        chamberId(newChamberId, oldChamberId) {
            this.searchSchedule();
        },
        dateTime(newChamberId, oldChamberId) {
            this.searchSchedule();
        },
    },

    computed:{
        schedules(){
            return this.$store.state.schedule.schedules;
            
        },
        suportType(){
            const type = this.$store.state.schedule.previousHistory;
            return type.support_types;
        },
        availableDates() {
            const schedules = this.$store.state.schedule.schedules
            return schedules.dates ? Object.values(schedules.dates).map(date => date.status ? date.date : '') : [];
        },
        lastAppointments(){
            return this.$store.state.schedule.previousHistory;
            return type;
            return type.support_types ? Object.values(type.support_types).map(typeId => typeId.id) : []; 
        },
        availableChamber() {
            const schedules = this.$store.state.schedule.schedules
            return schedules.chambers;
        },
        chamber(){
            const chambers = Object.values(this.availableChamber).filter(ch=>ch.id == this.chamberId);
            const chamber =  Object.values(chambers).map(chamber => chamber.name);
            return chamber;
        },
        availableTime() {
            const schedules = this.$store.state.schedule.schedules
            return schedules.schedules ? Object.values(schedules.schedules).map(schedule => schedule) : [];
        },

        time(){
            const times = Object.values(this.availableTime).filter(ch=>ch.id == this.scheduleId);
            console.log('asdfase',this.scheduleId);
            const time =  Object.values(times).map(time => time.time_schedule)  
            return time;
        },

        stimeEtime(){
            const stime =  Object.values(this.time).map(stime => stime.start_time);
            const etime =  Object.values(this.time).map(stime => stime.end_time);
            return `${stime} - ${etime}`;

        },
         attrs(){
            const schedules = this.$store.state.schedule.schedules
            const dates = schedules.dates ? Object.values(schedules.dates).map(date => date.status ? date.date : '') : [];
            return [
                {
                    bar: {
                        style:{
                             backgroundColor: 'green',
                        }
                    },
                    dates: dates,
                }
            ];
        },
        dates: {
            get() {
                return this.formatDate(this.date)
            }
        }
    },

    methods: {

        async postData(){
            console.log(this.scheduleId, this.mentorType, this.questions)
            if(this.scheduleId && this.mentorType && this.user_id){
                await axios().post('http://192.168.88.189:8080' + '/api/appointments', {
                    schedule_id: this.scheduleId,
                    type:  this.mentorType,
                    user_id: this.user_id,
                    questions: this.questions
    
                }).then( () => {
                     this.$router.push( `/profile/${this.user_id}`);
                } );
            }
            else{
               
                if(!this.mentorType){
                    alert('Mentor Type Must Be Select')
                }
                if(!this.chamberId){
                    alert('Chamber Must Be Select')
                }
                if(!this.scheduleId){
                    alert('Time Must Be Select')
                }
            }
            
        },

        searchSchedule() {
            console.log(this.availableChamber[0])
            this.$store.dispatch('schedule/getSchedules', {
                chamber_id: this.chamberId || '',
                date: this.date ? this.formatDate(this.date) : '',
                scheduleId: this.scheduleId || '',
            });
        },

        searchScheduleByChamber() {
            this.$store.dispatch('schedule/getSchedules', {
                chamber_id: this.chamberId || '',
            });
        },

        formatDate(input) {

            let date = new Date(input);
            let month = '' + (date.getMonth() + 1);
            let day = '' + date.getDate();
            let year = date.getFullYear();

            if (month.length < 2) {
                month = '0' + month;
            }

            if (day.length < 2) {
                day = '0' + day;
            }
                
            return [year, month, day].join('-');
        },
        
        addNewField(){
            this.questions.push("");
        },

        removeField(index){
            this.questions.splice(index, 1)
        },

        addQuestions() {
            console.log('asche', this.questions);
            this.questions.push({
                question: "",
            });
        },
        
        remove(index){
            this.questions.splice(index, 1);
        }
        
    }
}
</script>